<template>
  <section class="v-catalog">
    <h2>{{ msg }}</h2>
    <v-catalog-menu :categories="productCategories" @select="selectCategory" />
    <div class="v-catalog_items" v-if="this.sortedProducts.length">
      <v-catalog-item
        v-for="product in sortedProducts"
        :key="product.id"
        :product_data="product"
        @addToCart="addToCart"
      />
    </div>
    <!-- show preloader if data is not downloaded -->
    <div v-else>waite producte ...</div>
    <v-notification :messages="messages" />
  </section>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import vCatalogMenu from '@/components/v-catalog-menu'
import vCatalogItem from '@/components/v-catalog-item'
import vNotification from '@/components/notifications/v-notification'

export default {
  name: 'v-catalog',
  components: {
    vCatalogMenu,
    vCatalogItem,
    vNotification
  },
  props: {
    msg: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      sortedProducts: [],
      // messages: [{ name: 'notification name', id: Date.now().toLocaleString() }]
      messages: []
    }
  },
  computed: {
    ...mapGetters(['GET_SPREADSHEETS', 'GET_PROCESSED_SPREADSHEETS']), //     store     Google 
    productCategories () {
      return [
        ...new Set( //         
          this.GET_PROCESSED_SPREADSHEETS.map(({ category }) => category)
        )
      ]
    }
  },
  methods: {
    ...mapActions([
      'ACT_SPREADSHEETS_FROM_API', //      api     vuex
      'ACT_PROCESSED_SPREADSHEETS_TO_STORE', //   ,   
      'ACT_ADD_TO_CART' //    store ,  
    ]),
    addToCart (data) {
      this.ACT_ADD_TO_CART(data).then(() => {
        const timeStamp = Date.now().toLocaleString() //  id       
        this.messages.unshift({
          //  push,    :     
          name: data.category + ' added to cart',
          id: timeStamp
        })
      })
    },
    selectCategory (valueCategory) {
      this.sortedProducts = [...this.GET_PROCESSED_SPREADSHEETS] //    

      if (valueCategory !== 'all') {
        // ,      ,    
        //    - ,     
        this.sortedProducts = this.sortedProducts.filter(function (product) {
          return product.category === valueCategory
        })
      } else {
        return this.GET_PROCESSED_SPREADSHEETS
      }
    },
    productsFromSpreadsheets () {
      //       
      const gsx = this.GET_SPREADSHEETS.feed.entry //    json    
      const arr = gsx.map((obj, index) => {
        // map            
        return {
          //            ,   
          id: index,
          published: obj.gsx$published.$t,
          category: obj.gsx$category.$t,
          title: obj.gsx$title.$t,
          description: obj.gsx$description.$t,
          price: obj.gsx$price.$t,
          img: obj.gsx$imglink.$t.split('/view?')[0].split('d/')[1], //    
          cream_type: obj.gsx$creamtype.$t.split(', '), //            
          cream_type_selected: obj.gsx$creamtype.$t.split(', ')[0], //        
          cream_flavor: obj.gsx$creamflavor.$t.split(', '),
          cream_flavor_selected: obj.gsx$creamflavor.$t.split(', ')[0],
          uniqueProductWithOptions:
            index +
            obj.gsx$creamtype.$t.split(', ')[0] +
            obj.gsx$creamflavor.$t.split(', ')[0]
        }
      })
      this.ACT_PROCESSED_SPREADSHEETS_TO_STORE(arr)
    }
  },
  mounted () {
    this.ACT_SPREADSHEETS_FROM_API() //    ,   api    json  Google 
      .then(response => {
        if (response.data) {
          console.log(
            '%c%s',
            'background-color: #A63E51; color: white; font: 1rem/1 Tahoma; padding: 1px 5px',
            'Cakes from the DataBase loaded!'
          )
          this.productsFromSpreadsheets() //       
          this.sortedProducts = [...this.GET_PROCESSED_SPREADSHEETS]
        }
      })
  }
}
</script>

<style lang="scss">
.v-catalog {
  max-width: $screenwidth * 2;
  margin: auto;
  h2 {
    font-family: 'Concert One', cursive;
  }
  &_items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
</style>
