import axios from 'axios'

export default {
  ACT_SPREADSHEETS_FROM_API ({ commit }) {
    const url =
      'https://spreadsheets.google.com/feeds/list/1PVWNPVPAZ8lPTJW4CkW95ybnRxT6G_SSX3RNNDdkWgg/1/public/full?alt=json'
    return axios(url, {
      method: 'GET' //    Google 
    })
      .then(feed => { //    
        commit('MUT_SPREADSHEETS_TO_STATE', feed.data) //     data  feed
        return feed
      })
      .catch(error => { //   
        console.log(error)
        return error
      })
  }
}
