<template>
  <div id="app" class="app">
    <main>
      <small class="app_window-size"
        >window size: {{ this.GET_WINDOW_TYPE }}</small
      >
      <v-header />
      <router-view />
    </main>
    <v-footer />
  </div>
</template>

<script>
import vHeader from '@/components/v-header.vue'
import vFooter from '@/components/v-footer.vue'
import { mapActions, mapGetters } from 'vuex'

export default {
  components: {
    vHeader,
    vFooter
  },
  data () {
    return {
      windowWidth: 1200, //  
      windowType: '' //  
    }
  },
  computed: {
    ...mapGetters(['GET_WINDOW_TYPE'])
  },
  methods: {
    ...mapActions(['ACT_WINDOW_SIZE'])
  },
  mounted () {
    const vm = this //   ,     
    function switchWindowType (ww) {
      //    
      if (ww >= 1200) {
        return 'Extra large'
      } else if (ww >= 992 && ww <= 1199) {
        return 'Large'
      } else if (ww >= 768 && ww <= 991) {
        return 'Medium'
      } else if (ww >= 576 && ww <= 767) {
        return 'Small'
      } else {
        return 'Extra small'
      }
    }
    function listenWindowSize () {
      // ,    
      vm.windowWidth = window.innerWidth
      vm.windowType = switchWindowType(vm.windowWidth) //      ,     
      vm.ACT_WINDOW_SIZE(vm.windowType) //       
    }
    listenWindowSize() //     
    window.addEventListener('resize', function () {
      //      
      listenWindowSize()
    })
  }
}
</script>

<style lang="scss">
html {
  height: 100%;
}
body {
  height: 100%;
  margin: 0;
}
#app,
.app {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  &_window-size {
    position: fixed;
    left: 5px;
  }
}
</style>
